/* http://meyerweb.com/eric/tools/css/reset/
   v3.0 | 20180413
   License: none (public domain)
*/
@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}
.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-brush:before { content: "\E6E5";
}
.icon-homepage:before { content: "\E702";
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}
/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
.mb10 {
  margin-bottom: 10px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100%;
}
body, html {
  height: 100%;
  font-family: "Microsoft Yahei", Arial, sans-serif;
}
#app {
  height: 100%;
  background: #f0f2f5;
}

.layout {
  height: 100%;
}
.layout-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    width: 100%;
    height: 50px;
    color: rgba(0, 0, 0, 0.65);
    background: #fff;
    -webkit-box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
            box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}
.layout-header__user {
      float: right;
      margin-right: 30px;
      line-height: 50px;
}
.layout-header__loginOut {
      margin-left: 30px;
      vertical-align: top;
      font-size: 12px;
}
.layout-header__loginOut:hover {
        color: #409EFF;
}
.layout-left {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;
    width: 180px;
    height: 100%;
    background: #001529;
    -webkit-box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
            box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}
.layout-left__logo {
      height: 50px;
      background: url(data:image/png;base64,) no-repeat;
      background-color: #002140;
      background-size: auto 30px;
      background-position: center;
}
.layout-left__content {
      padding: 10px 0;
}
.layout-left__item {
      height: 40px;
      padding-left: 30px;
      color: rgba(255, 255, 255, 0.8);
      font-size: 14px;
      line-height: 40px;
      cursor: pointer;
}
.layout-left__item:hover {
        color: white;
}
.layout-left__item.active {
        background: #1890ff;
}
.layout-left__item .iconfont {
        vertical-align: middle;
}
.layout-left__item span {
        vertical-align: middle;
}
.layout-content {
    height: 100%;
    padding: 50px 0 0 180px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.login {
  height: 100%;
  text-align: center;
}
.login::after {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}
.login-container {
    display: inline-block;
    width: 1000px;
    height: 500px;
    background: #fff;
    border-radius: 4px;
    vertical-align: middle;
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.06);
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.06);
}
.login-container::after {
      content: '';
      display: inline-block;
      height: 100%;
      vertical-align: middle;
}
.login-form {
    display: inline-block;
    width: 400px;
    padding: 20px 32px;
    vertical-align: middle;
}
.login-form__title {
      padding: 20px 0;
      font-size: 22px;
      color: #606266;
}

.login-form__captcha {
  float: left;
  width: 200px;
}
.login-form__captchaPng {
  float: left;
  height: 40px;
  margin-left: 15px;
  border: 1px solid #dcdfe6;
  cursor: pointer;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.home {
  width: 100%;
  height: 100%;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
